import React from 'react'
import { Card, Form, Button } from 'semantic-ui-react'
import Header from '../../components/header/Header'
import { Link } from 'react-router-dom'

export default function Recover() {
  return (
    <div>
      <Header />
      <Card style={{ margin: '0 auto', marginTop: '70px', padding: '10px', maxWidth: '420px', width: '100%' }}>
        <Card.Content>
          <Card.Header>Востанавление</Card.Header>
        </Card.Content>
        <Card.Content extra>
          <Form>
            <Form.Input label='Имя пользователя' placeholder='joe@schmoe.com' />

            <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
              <Link to='/login'><Button primary>Назад</Button></Link>
              <Button primary>Далее</Button>
            </div>

          </Form>
        </Card.Content>
      </Card>
    </div>
  )
}
