import React, {Component} from 'react'
import { Card, Form, Button, Message } from 'semantic-ui-react'
import { NavLink, withRouter } from 'react-router-dom'
import Header from '../../components/header/Header'

class Login extends Component{
  state = {
    login: "",
    pwd: "",
    loginError: false,
    pwdError: false
  }

  handleChange = (event, {name, value}) => {
    this.setState({
      [name]: value
    })
  }


  handleClick = () => {
    const { login, pwd } = this.state;

    !login.trim() ? this.setState({loginError: true}) :  this.setState({loginError: false})  
    !(pwd.trim().length > 3) ? this.setState({pwdError: true}) : this.setState({pwdError: false})

    if(login.trim() && (pwd.trim()).length > 3){
      if(login === 'test' && pwd === 'test'){
        this.props.history.push('/admin')
      }
    }
    
  }

  render(){
    const { login, pwd, loginError, pwdError } = this.state;
    return (
      <div>
        <Header />
        <Card style={{margin: '0 auto', marginTop: '70px', padding: '10px', maxWidth: '420px',width: '100%'}}>
          <Card.Content>
            <Card.Header>Логин</Card.Header>
          </Card.Content>
         <Card.Content extra>
          <Form error={ loginError || pwdError}>
            <Form.Input 
              label='Имя пользователя' 
              placeholder='test'
              name="login" 
              value={login}
              onChange={this.handleChange}
              error={ loginError }
            />
            {
              loginError && (
              <Message
                error
                header='Ошибка'
                content='Ошибка email'
              />
            )
            }
            <Form.Input 
              label='Пароль' 
              type='password' 
              placeholder='test'
              name="pwd"
              value={pwd}
              onChange={this.handleChange}
              error={ pwdError }
            />
            {
              pwdError && (
              <Message
                error
                header='Ошибка'
                content='Минимальная длина 3 символов'
              />
            )
            }

            <div style={{display: 'flex', justifyContent: 'space-between',alignItems: 'center'}}>
              <NavLink to='/recover' style={{color: '#4183c4'}}>Забыли пароль?</NavLink>
              <Button primary onClick={this.handleClick}>Войти</Button>
           </div>
            </Form>
          </Card.Content>
        </Card>
      </div>
    )
  }
}

export default withRouter(Login);
