import React from 'react'
import { NavLink } from 'react-router-dom'
import { Icon } from 'semantic-ui-react'
import { withRouter } from 'react-router-dom'
import './_.scss'

function Header(props) {
  return (
    <div>
        <header>
        <nav className='main-nav'>
          <div className='left-side'>
            <NavLink exact to='/' className='main-logo'></NavLink>
            {
              props.history.location.pathname.includes('/admin') && 
              <Icon className='menu-bars' name='bars' size='big' style={{marginLeft: '5px'}} onClick={() => { props.test() }}/>
            }
            
          </div>
          {
            props.history.location.pathname.includes('/admin') ?
            <ul>
              <li>
                <NavLink activeClassName='active-link' exact to='/'>Выйти</NavLink>
              </li>
            </ul>
            :
          <ul>
              <li>
                <NavLink activeClassName='active-link' exact to='/login'>Логин</NavLink>
              </li>
              <li>
                <NavLink activeClassName='active-link' exact to='/register'>Регистрация</NavLink>
              </li>
          </ul>
          }

        </nav>

        <nav className='content-nav'>
        </nav>
        </header>
    </div>
  )
}

export default withRouter(Header)
