import React, { Component } from 'react'
import Header from '../../components/header/Header'
import { Sidebar, Menu } from 'semantic-ui-react'
import { withRouter, NavLink, BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Profile from '../profile/Profile'
import Analytics from '../analytics/Analytics'
import Shops from '../shops/Shops'
import Storage from '../storage/Storage'
import Employers from '../employers/Employers'

class Admin extends Component {
  state = {
    visibility: false
  }
  test = () => {
    this.setState(state => ({
      visibility: !state.visibility
    }))
  }
  render() {
    return (
      <div>
        <Header test={this.test}/>
        <Router>
        <Sidebar
            style={{top: 'auto', width: '210px'}}
             as={Menu}
             animation='overlay'
             icon='labeled'
             inverted
             vertical
             visible={this.state.visibility}
             width='thin'
        >
          <NavLink to='/admin/profile' className='item'>Личная страница</NavLink>
          <NavLink to='/admin/stats' className='item'>Аналитика</NavLink>
          <NavLink to='/admin/shops' className='item'>Владельцы магазина</NavLink>
          <NavLink to='/admin/storage' className='item'>Склад</NavLink>
          <NavLink to='/admin/employers' className='item'>Сотрудники</NavLink>
        </Sidebar>


        <h1 style={{textAlign: 'center', paddingTop: '40px'}}>Страница админа</h1>

          <Switch>
            <Route exact path='/admin/profile' component={Profile}></Route>
            <Route exact path='/admin/stats' component={Analytics}></Route>
            <Route exact path='/admin/shops' component={Shops}></Route>
            <Route exact path='/admin/storage' component={Storage}></Route>
            <Route exact path='/admin/employers' component={Employers}></Route>
          </Switch>
        </Router>

       
      </div>
    )
  }
}

export default withRouter(Admin)
