import React from 'react'
import { Card, Form, Button } from 'semantic-ui-react'
import Header from '../../components/header/Header'

export default class Register extends React.Component {
  state = {
    step: 1
  }
  next = (n) => {
    this.setState({step: n})
  }
  render(){
    return (
      <div>
        <Header />
        <Card style={{ margin: '0 auto', marginTop: '70px', padding: '10px', maxWidth: '420px', width: '100%' }}>
          <Card.Content>
            <Card.Header>
              <p>Регистрация</p>
            </Card.Header>
          </Card.Content>
          <Card.Content extra>

          {
            this.state.step === 1 ?
            <>
              <p>Шаг 1</p>
              <Form>
                <Form.Input label='Имя пользователя' placeholder='joe@schmoe.com' />
                <Form.Input label='Пароль' type='password' />
          
                <Button primary style={{ display: 'block', marginLeft: 'auto' }} onClick={() => {this.next(2)}}>Регистрация</Button>
              </Form>
            </>
            :
            <>
              <p>Шаг 2</p>
              <Form>
                <Form.Input label='Имя пользователя' placeholder='joe@schmoe.com' />
          
                <div style={{display: 'flex', justifyContent: 'flex-end'}}>
                  <Button primary onClick={() => {this.next(1)}}>Назад</Button>
                  <Button primary >Регистрация</Button>
                </div>
              </Form>
            </>
    
          }

          </Card.Content>
        </Card>
      </div>
    )
  }
}





